import Vue from 'vue'
import _remove from 'lodash/remove'

const cache = {}
const keys = []

/**
 * 
 *
 * @param {string} name
 */
export const removeCacheByName = (name) => {
  if (!name) {
    return
  }

  const key = findKeyByName(name)
  if (!key) {
    return
  }

  cache[key].componentInstance.$destroy()

  delete cache[key]
  _remove(keys, (i) => i === key)
}

/**
 *  key
 * @param {string} name
 * @return {string}
 */
const findKeyByName = (name) => {
  for (const key of Object.keys(cache)) {
    const v = cache[key]
    if (v.componentOptions?.Ctor?.options?.name === name) {
      return key
    }
  }
}

//  vue devtools 
// 
export default Object.assign({}, Vue.options.components.KeepAlive, {
  name: 'LzKeepAlive',
  created() {
    this.cache = cache
    this.keys = keys
  },
  destroyed() {},
})
